

















import Vue from 'vue'
export default Vue.extend({
  props: {
    _onSubmit: {
      type: Function,
      required: true
    },
    isLoadingCreateBoard: {
      type: Boolean,
      required: true
    }
  },
  data () {
    return {
      title: '',
      content: ''
    }
  },
  methods: {
    onSubmit (e: Event) {
      e.preventDefault()
      this._onSubmit({
        title: this.title,
        content: this.content
      })
    }
  }
})
